export default function DatePicker(props) {
  const {
    className,
    placeholder,
    starAge,
    endAge,
    style,
    startMonth = 1,
    endMonth = 12,
    defaultValue = moment().format('YYYY-MM-DD'),
    show = false,
    onChange = () => {},
  } = props;

  /* 年月日设置 */
  // const [
  //   ageList, // 获取设置的年区间
  //   mouthList, // 获取设置的月区间，默认12个月
  //   weekList, // 星期日到星期一列表
  //   selectedAge, // 选择的年
  //   setSelectedAge, // 选择的年hook
  //   selectedMouth, // 选择的月数
  //   setSelectedMouth, // 选择的月数hook
  //   selectedDay, // 选择的日数
  //   setSelectedDay, // 选择的日数hook
  //   week, // 当前选择时间是星期几
  //   endDay, // 当前月的最后一天
  //   // inputValue, // 选择日期后显示的值
  //   // setInputValue, // 选择日期后显示的值hook
  //   // status, // 表单的状态
  //   // setStatus, // 表单的状态hook
  // ] = calculate(
  //   defaultValue,
  //   placeholder,
  //   startMonth,
  //   endMonth,
  //   starAge,
  //   endAge
  // );
  const [Tage, Tmonth, Tday, Status] = getTime(defaultValue); // 默认年月日
  const ageList = Vdata(starAge, endAge); // 获取设置的年区间
  const mouthList = Vdata(startMonth, endMonth); // 获取设置的月区间，默认12个月
  const weekList = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
  ]; // 星期日到星期一列表

  const [selectedAge, setSelectedAge] = useState(Tage); // 年
  const [selectedMouth, setSelectedMouth] = useState(Tmonth); // 月
  const [selectedDay, setSelectedDay] = useState(Tday); // 天
  const _month = moment(`${selectedAge}-${selectedMouth}`); // 获取选择后的年月，已此计算得到当月有多少天
  // const startDay = _month.startOf('month').format('DD');//当前月的第一天
  const endDay = Number(_month.endOf('month').format('DD')); // 当前月的最后一天 ,这里需要 Number 处理
  const week = _month.startOf('month').format('dddd'); // 当前选择时间是星期几
  const _value = placeholder ? '' : defaultValue;
  const _Status = placeholder ? 'normal' : Status;
  const [inputValue, setInputValue] = useState(_value); // 默认input
  const [status, setStatus] = useState(_Status); // 默认状态
  // 点击 - 年
  const FselectedAge = (e, itemAge) => {
    const value = `${itemAge}-${VpadLeft(selectedMouth)}-${VpadLeft(
      selectedDay
    )}`;
    setSelectedAge(itemAge); // 更新弹窗时间实时预览
    setStatus('false'); // 更新显示状态
    setInputValue(value); // input 显示的值
    e.preventDefault(); // 取消冒泡
  };
  // 点击 - 月
  const FselectedMouth = (e, itemMouth) => {
    const value = `${selectedAge}-${VpadLeft(itemMouth)}-${VpadLeft(
      selectedDay
    )}`;
    setSelectedMouth(itemMouth); // 更新弹窗时间实时预览
    setInputValue(value); // input 显示的值
    setStatus('false'); // 更新显示状态
    e.preventDefault(); // 取消冒泡
  };

  // 点击 - 日
  const FselectedDay = (event, itemDay) => {
    const value = `${selectedAge}-${VpadLeft(selectedMouth)}-${VpadLeft(
      itemDay
    )}`;
    setSelectedDay(itemDay); // 更新弹窗时间实时预览
    setInputValue(value); // input 显示的值
    setStatus('false'); // 更新显示状态
    setIsShow(false); // 关闭弹窗
    event.preventDefault(); // 取消冒泡
  };
  // 传递表单状态给父级,判断日期格式是否正确
  Stf(status);

  // 将数据传显示给input
  onChange(selectedAge, selectedMouth, VpadLeft(selectedDay));

  // 弹窗显示隐藏
  const [isShow, setIsShow] = useState(show);

  const close = () => {
    setIsShow(false); // 关闭弹窗
    return isShow;
  };

  // 默认class
  const cls = classNames(className, `${Variable}-datepicker`);
  return (
    <Fragment>
      <Overlay
        show={show}
        close={close}
        cuClassName={cls}
        cuStyle={style}
        render={
          <div className={`${Variable}-calendar`}>
            <div className={`${Variable}-calendar__top`}>
              <div className={`${Variable}-calendar__top-text`}>
                <span>{selectedAge}年</span>
                <span>{VpadLeft(selectedMouth)}月</span>
                <span>{VpadLeft(selectedDay)}号</span>
              </div>
              <div className={`${Variable}-calendar__top-op`}>
                <Icon type="close" hover size="16" onClick={close} />
              </div>
            </div>
            <div className={`${Variable}-calendar__body`}>
              {/* 年 */}
              <div className={`${Variable}-calendar__age`}>
                {ageList.map((itemAge, index) => (
                  <div
                    className={classNames(
                      `${Variable}-calendar__row`,
                      itemAge === Number(selectedAge) ? 'is-selected' : ''
                    )}
                    key={index}
                    onClick={(event) => FselectedAge(event, itemAge)}
                  >
                    <div className={`${Variable}-calendar__cell`}>
                      <span>{itemAge}年</span>
                    </div>
                  </div>
                ))}
              </div>
              {/* 月 */}
              <div className={`${Variable}-calendar__month`}>
                {mouthList.map((itemMouth, index) => (
                  <div
                    className={classNames(
                      `${Variable}-calendar__row`,
                      itemMouth === Number(selectedMouth) ? 'is-selected' : ''
                    )}
                    key={index}
                    onClick={(event) =>
                      FselectedMouth(event, VpadLeft(itemMouth))
                    }
                  >
                    <div className={`${Variable}-calendar__cell`}>
                      <span>{VpadLeft(itemMouth)}月</span>
                    </div>
                  </div>
                ))}
              </div>
              {/* 日 */}
              <div className={`${Variable}-calendar__day`}>
                <div className={`${Variable}-calendar__day-header`}>
                  <div className={`${Variable}-calendar__row`}>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>日</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>一</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>二</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>三</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>四</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>五</span>
                    </div>
                    <div className={`${Variable}-calendar__cell`}>
                      <span>六</span>
                    </div>
                  </div>
                </div>
                <div className={`${Variable}-calendar__day-body`}>
                  <div className={`${Variable}-calendar__row`}>
                    {weekList.map(
                      (item, index) =>
                        item === week &&
                        [...Array(index)].map((item, index) => (
                          <div
                            className={`${Variable}-calendar__cell`}
                            key={index}
                          >
                            <span></span>
                          </div>
                        ))
                    )}
                    {!!endDay &&
                      [...Array(endDay)].map((itemDay, index) => (
                        <div
                          className={classNames(
                            `${Variable}-calendar__cell`,
                            Number(index + 1) === Number(selectedDay)
                              ? 'is-selected'
                              : ''
                          )}
                          key={index}
                          onClick={(event) =>
                            FselectedDay(event, Number(index + 1))
                          }
                        >
                          <span>{index + 1}</span>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        }
      >
        <Fragment>
          <div className={`${Variable}-datepicker__input`}>
            <Input
              placeholder={placeholder}
              value={inputValue}
              onChange={onChange}
            />
            <Icon type="date" size="16" />
          </div>
        </Fragment>
      </Overlay>
    </Fragment>
  );
}
